caption {
  text-align: left;
}

td,
th {
  vertical-align: top;
  padding: 0;
}

th {
  text-align: left;
  font-weight: bold;
}

.l-header {
  position: fixed;
  top: 0;
  left: 0%;
  z-index: 1;
  width: 100%;
  height: clamp(4.5rem, -1.7451923077rem + 26.6461538462vw, 31.5625rem);
}
.l-header__body {
  padding: 0.5625rem 1.125rem;
}
.l-nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 18.75rem;
  height: 100vh;
  z-index: 3;
  transform: translateX(-300px);
  transition: transform 0.3s;
}
.l-nav__list {
  margin: 2.1875rem 0 0 1.5625rem;
}
.l-nav__item:not(:first-of-type) {
  margin-top: 1.25rem;
}
.l-nav__link {
  font-size: 0.875rem;
}
.l-nav__sns-list {
  margin: 3.125rem 0 0 1.5625rem;
}
.l-nav__sns-item {
  margin-top: 1.25rem;
}
.l-nav__sns-link {
  font-size: 0.875rem;
}

.l-hamburger-menu {
  position: fixed;
  top: 1.4375rem;
  right: 1.125rem;
  width: 1.875rem;
  height: 1.625rem;
  z-index: 4;
}
.l-hamburger-menu__line {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.875rem;
  height: 0.125rem;
  transition: all 0.3s;
}
.l-hamburger-menu__line:nth-child(1) {
  top: calc(50% - 10px);
}
.l-hamburger-menu__line:nth-child(3) {
  top: calc(50% + 10px);
}

.l-mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 2;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s;
}

.l-video-body {
  height: 80vh;
}
@media (min-width: 992px) {
  .l-video-body {
    width: 100%;
    height: 100vh;
  }
}

.l-video {
  top: 4.5rem;
  height: 62.5rem;
  left: 0;
  right: 0;
  margin: 0 auto;
}
@media (min-width: 992px) {
  .l-video {
    position: fixed;
    top: -13.75rem;
    z-index: -10;
  }
}

.l-sp-only {
  display: block;
}
@media (min-width: 992px) {
  .l-sp-only {
    display: none;
  }
}

.m-section-title {
  font-size: clamp(1.25rem, 1.1346153846rem + 0.4923076923vw, 1.75rem);
  font-weight: bold;
}

.m-container {
  max-width: 56.25rem;
  margin: 0 auto;
  padding: 0 1.25rem;
}

.l-nav.is-active {
  transform: none;
}

.l-hamburger-menu.is-active > .l-hamburger-menu__line:nth-child(1) {
  top: 50%;
  transform: translate(-50%, -50%) rotate(-315deg);
}

.l-hamburger-menu.is-active > .l-hamburger-menu__line:nth-child(2) {
  opacity: 0;
}

.l-hamburger-menu.is-active > .l-hamburger-menu__line:nth-child(3) {
  top: 50%;
  transform: translate(-50%, -50%) rotate(315deg);
}

.l-mask.is-active {
  visibility: visible;
  opacity: 1;
}

:root {
  --base-color:#FFF;
  --text-color:#121212;
  --mask-color:#313131;
  --stepBg-color:#414141;
  --stepBar-color:#FFDD00;
}

body {
  color: var(--text-color);
}

.theme-header {
  background-color: var(--base-color);
}

.theme-hamburger-menu-line {
  background-color: var(--text-color);
}

.theme-nav {
  background-color: var(--base-color);
}

.theme-mask {
  background-color: var(--mask-color);
}

.theme-section-title {
  color: var(--base-color);
}

.theme-project-bg {
  background: linear-gradient(rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.8));
}